html {
  scroll-behavior: smooth;
}

html,
body {
  font-family: 'Roboto', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  color: $color-primary;
  padding: 0;
  margin: 0;

  * {
    box-sizing: border-box;
  }
}

table {
  border-collapse: collapse;
}

i,
em {
  font-family: inherit;
  font-weight: inherit;
  font-size: inherit;
}

b,
strong {
  font-family: inherit;
  font-weight: bold;
  font-size: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  font-weight: bold;
}

a {
  text-decoration: none;
  color: $color-link;
  transition: color 0.2s ease;
  font-size: inherit;

  &:hover {
    text-decoration: underline;
    color: $color-link-hover;
  }
}

.container {
  @include maxwidth;

  @include desktop {
    padding-left: 20px;
    padding-right: 20px;
  }
}

img {
  max-width: 100%;
  height: auto;
}

h1 {
  font-size: 2rem;
}

h2 {
  line-height: 1.25em;
  margin-bottom: 1.5rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid $color-black;
  text-transform: uppercase;
  font-size: 1.625rem;
  margin-top: 3rem;
}

h3 {
  font-size: 1.5rem;
  margin-top: 3rem;
}

p {
  line-height: 1.4;
  font-size: 1.125rem;
  margin-bottom: 1.75rem;

  &:last-child {
    margin-bottom: 0;
  }
}

ol,
ul {
  padding-left: 1rem;
  margin-top: 0;
  margin-bottom: 1.5rem;

  li {
    font-size: 1.125rem;
    margin-bottom: 0.75rem;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

figure {
  text-align: center;
  margin-bottom: 1.5rem;

  figcaption {
    margin-top: 1rem;
    font-size: 0.875rem;
  }
}

.visually-hidden {
  @include visuallyhidden;
}

[class*='notation-'] {
  line-height: 1.75rem;
}

.caption {
  margin-top: 1rem;
  font-size: 1rem;
  color: rgba($color-black, 0.6);

  b,
  strong {
    color: $color-black;
  }
}

.subhead {
  font-size: 1.375rem;
}

.box {
  background: $color-grey;
  padding: 1rem;

  @include tablet {
    .separator {
      display: none;
    }
  }

  h2 {
    border-bottom: 0;
    font-size: 1rem;
    text-transform: none;
    margin-bottom: 0.5rem;
    padding-bottom: 0;
    margin-top: 0;
  }

  p,
  ul {
    margin-top: 0;
    margin-bottom: 1rem;

    @include tablet {
      font-size: 1rem;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  ul {
    list-style-type: none;
    display: flex;
    flex-wrap: wrap;
    padding-left: 0;
    margin-bottom: 0;
    list-style-position: inside;

    @include tablet {
      display: block;
    }

    li {
      @include tablet {
        font-size: 1rem;
      }

      a {
        text-transform: uppercase;

        @include tablet {
          font-size: 1rem;
        }
      }
    }
  }
}

.mt {
  &-sm {
    margin-top: 1rem;
  }

  &-md {
    margin-top: 2rem;
  }

  &-lg {
    margin-top: 3.5rem;
  }
}

.mb {
  &-sm {
    margin-bottom: 1rem;
  }

  &-md {
    margin-bottom: 2rem;
  }

  &-lg {
    margin-bottom: 3.5rem;
  }
}

.contents {
  ul {
    list-style-type: upper-roman;
  }
}

.header-info {
  display: flex;
  align-items: center;
  margin-bottom: 1rem;

  @include tablet {
    flex-wrap: wrap;
  }
}

.header-left {
  display: flex;
  padding: 1rem;
  flex: 1 0 0%;

  @include tablet {
    flex: 1 0 100%;
    width: 100%;
  }
}

.header-authors {
  width: 60%;
  flex: 0 0 60%;
  padding-right: 15px;

  @include tablet-min {
    &:first-child {
      width: 70%;
      flex: 0 0 70%;
    }
  }

  @include tablet {
    li {
      font-size: 1rem;
    }
  }

  ul {
    display: block;
  }
}

.header-published {
  width: 40%;
  flex: 0 0 40%;

  @include tablet-min {
    &:last-child {
      width: 30%;
      flex: 0 0 30%;
    }
  }

  @include tablet {
    p {
      font-size: 1rem;
    }
  }
}

.header-legend {
  display: flex;
  align-items: center;
  font-size: 0.875rem;
  padding: 0.5rem;
  border-radius: 0.7rem;
  border: 2px solid $color-link;
  flex: 1 0 100%;
  width: 100%;
  margin-top: 15px;

  @include tablet-min {
    flex: 0 0 270px;
    width: 270px;
    margin-left: 2rem;
    margin-top: 0;
  }

  svg {
    margin-right: 0.3rem;
  }

  p {
    margin: 0;
    position: relative;
    top: 1px;
    font-size: 1rem;
  }
}

.click-icon {
  width: 1.625rem;
  height: auto;
}

.references {
  span {
    color: rgba($color-black, 0.6);
    display: block;
    margin-top: 0.4rem;
  }
}

.static {
  @include tablet {
    max-width: 100%;
    overflow: auto;
    display: flex;
  }
}

.framed {
  border: 1px solid $color-blue;
  border-radius: 0.75rem;
  padding: 1rem 1.75rem;

  @include tablet {
    max-width: 100%;
    overflow: auto;
    display: flex;
  }

  .eyebrow {
    font-size: 1.125rem;
    color: $color-blue;
    padding-left: 3rem;
    padding-right: 3rem;
    margin-bottom: 2rem;
    min-height: 2.375rem;
    background: url('../img/icons/click-icon.svg') no-repeat top left /
      1.5625rem auto;

    @include tablet {
      padding-right: 0;
    }
  }

  .caption {
    text-align: left;
    color: $color-blue;
    padding-left: 3rem;
    padding-right: 3rem;
    font-size: 1rem;

    @include tablet {
      padding-right: 0;
      padding-left: 0;
    }

    b,
    strong {
      display: block;
      color: $color-blue;
    }

    &-top {
      background: #b3cddb;
      padding: 0.15rem 1rem;
      border-radius: 0.25rem;
      margin-bottom: 1.5rem;
      display: inline-block;
      font-size: 0.875rem;

      &.default {
        padding: 0.15rem 0;
        margin-bottom: 0;
      }

      &-wrapper {
        text-align: center;
      }
    }
  }
}

.formula {
  &-wrapper {
    @include tablet {
      display: block;
      max-width: 100%;
      overflow: auto;
      padding-bottom: 20px;
    }
  }

  tbody {
    tr {
      border-bottom: 1px solid rgba($color-black, 0.2);

      &:last-child,
      &:first-child {
        border-bottom: 1px solid rgba($color-black, 0.6);
      }
    }

    th,
    td {
      padding: 0.5rem;
      font-size: 1.125rem;
    }

    td {
      color: rgba($color-black, 0.6);
    }
  }
}

.inline-math {
  .MathJax_Display,
  .MJXc-display {
    display: inline !important;
  }
}

.math-left {
  .mjx-chtml {
    text-align: left !important;
  }
}

.math-right {
  .mjx-chtml {
    text-align: right !important;
  }
}

.cite {
  color: $color-blue;
  cursor: pointer;

  &:not(.show) {
    display: none;
  }

  &-parent {
    position: relative;
  }

  &-content {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    padding: 15px;
    border-radius: 5px;
    border: 1px solid $color-blue;
    background: $color-white;
    color: $color-black;
  }

  &:focus,
  &:hover {
    .cite-content {
      display: block;
    }
  }
}

.scroll-top {
  display: none;
  position: fixed;
  bottom: 20px;
  right: 20px;
  background: $color-blue;
  width: 40px;
  align-items: center;
  justify-content: center;
  height: 40px;

  @include tablet {
    right: 10px;
    bottom: 10px;
  }

  svg {
    width: 30px;
  }
}

input[type='range'],
.ui-slider,
div.ui-slider .ui-slider-handle,
.framed .matrixsmall,
#run-ea-button,
select,
input[type='button']:not(:disabled),
.framed [class*='chessboard'] {
  cursor: pointer;
}

.no-pointer {
  &,
  * {
    cursor: default !important;
  }
}

.ui-state-error {
  padding: 1rem !important;

  p {
    margin-top: 0;
  }
}
